var timer = null
var curToken
import {
	uniSearchBar,
	uniIcons
} from '../../components/uni-icons/uni-icons.vue'
import {
	CALLRECORDLISTTODAY,
	CALLRECORDLIST,
	SUBSCRIBEINFO
} from '../../utils/api.js'
import uniLoadMore from '../../components/uni-load-more/uni-load-more.vue'
import uniNoticeBar from '../../components/uni-notice-bar/uni-notice-bar.vue'
export default {
	components: {
		uniSearchBar,
		uniNoticeBar,
		uniIcons,
		uniLoadMore
	},
	filters: {
		formateTel: function(value) {
			if (value == '057126211779') {
				var result = '魔小秘(示例电话)'
				return result
			} else {
				var pat = /(\d{3})\d*(\d{4})/
				var b = value.replace(pat, '$1****$2');
				return b
			}
		},
		formateDate: function(value) {
			var first = value.split(" ")[0]
			if (first !== undefined) {
				var second = first.split("-")
				var date = second[1] + "-" + second[2]
				return date
			}
		}
	},
	data() {
		return {
			todayStyle: 'border:1px solid #1c75ff;color:#1c75ff;box-shadow: 0 2rpx  10rpx #D4DDEC;',
			historyStyle: '',
			moduleData: [{
					src: '../../static/incomeHome/jietingpeizhi@2x.png',
					name: '接听配置'
				},
				{
					src: '../../static/incomeHome/yaoqing@2x.png',
					name: '立即邀请'
				},
				{
					src: '../../static/incomeHome/qianbao@2x.png',
					name: '我的钱包'
				}
			],
			callList: [{}],
			getToken: "",
			itemData: "",
			istoday: 1,
			todayText: 'more',
			historyText: 'more',
			page: 1
		}
	},
	onLoad(options) {
		this.initData()
		uni.showToast({
			title: '正在加载中...',
			icon: 'none',
			mask: true,
			isfocus: ''
		})
	},
	onPullDownRefresh() {
		if (this.istoday == 1) {
			this.initData()
		} else if (this.istoday == 0) {
			this.historyBtn()
		}

	},
	onReachBottom: function() {
		var _this = this
		if (timer != null) {
			clearTimeout(timer);
		}
		timer = setTimeout(function() {
			_this.getmore();
		}, 1000);
	},

	methods: {
		// gotoList今日历史记录
		gotoList() {
			uni.navigateTo({
				url: 'callList'
			})
		},
		// 跳转模块
		gotoModule(index) {
			console.log(index)
			if (index == 0) {
				uni.switchTab({
					url: '../SettingPage/SettingPage'
				})
			} else if (index == 1) {
				uni.navigateTo({
					url: '../Login/Invite/Invite'
				})
			} else if (index == 2) {
				uni.navigateTo({
					url: '../MinePage/MineWallet/MineWallet'
				})
			}
		},
		// 拨打电话
		call(tel) {
			uni.makePhoneCall({
				phoneNumber: tel
			})
		},
		// 初始化数据
		initData() {
			this.istoday = 1
			const params = {
				pageNum: 1
			}
			this.$request.url_request(CALLRECORDLISTTODAY, params, "GET", res => {
					this.itemData = JSON.parse(res.data).data.list
					uni.hideToast()
					uni.stopPullDownRefresh()
				},
				err => {});
		},
		// 加载更多
		getmore() {
			if (this.todayText == 'more' || this.historyText == 'more') {
				if (this.istoday == 1) {
					this.todayText = 'loading'
					this.$forceUpdate()
					this.page++
					const params = {
						pageNum: this.page
					}
					this.$request.url_request(CALLRECORDLISTTODAY, params, "GET", res => {
						if (JSON.parse(res.data).data.list.length <= 0) {
							this.todayText = 'noMore'
							this.$forceUpdate()
						} else {
							this.todayText = 'more'
							this.itemData = this.itemData.concat(JSON.parse(res.data).data.list)
							this.$forceUpdate()
						}
					}, err => {})
				} else {
					this.page++
					const params = {
						pageNum: this.page
					}
					this.historyText = 'loading'
					this.$request.url_request(CALLRECORDLIST, params, "GET", res => {
						if (JSON.parse(res.data).data.list.length <= 0) {
							this.historyText = 'noMore'
							this.$forceUpdate()
						} else {
							this.historyText = 'more'
							this.itemData = this.itemData.concat(JSON.parse(res.data).data.list)
							this.$forceUpdate()
						}
					}, err => {})
				}

			}
		},
		search() {
			uni.navigateTo({
				url: 'search/search'
			})
		},
		// 今日来电
		todayBtn() {
			uni.showToast({
				title: '正在加载中...',
				icon: 'none',
				mask: true
			})
			this.historyText = 'more'
			this.historyStyle = ''
			this.todayStyle = 'border:1px solid #1c75ff;color:#1c75ff;box-shadow: 0rpx 1rpx  10rpx #D4DDEC;'
			this.page = 1
			this.initData()
		},
		// 历史来电
		historyBtn() {
			uni.showToast({
				title: '正在加载中...',
				icon: 'none',
				mask: true
			})
			this.todayText = 'more'
			this.page = 1
			this.istoday = 0
			this.todayStyle = ''
			this.historyStyle = 'border:1px solid #1c75ff;color:#1c75ff;box-shadow: 0rpx 1rpx  10rpx #D4DDEC;'
			const params = {
				pageNum: 1
			}
			this.$request.url_request(CALLRECORDLIST, params, "GET", res => {
					this.itemData = JSON.parse(res.data).data.list
					uni.hideToast()
					uni.stopPullDownRefresh()
				},
				err => {});
		},
		gotoDetails(id) {
			uni.navigateTo({
				url: 'callDetails/callDetails?id=' + id
			})
		},

	}
}
